import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import CallTable from "../Table/callTable"
import Activity from "../Customer/Actvity"
import { Table,Button } from '@mui/material';
import TextField from '@mui/material/TextField';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DateTimePicker from '@mui/lab/DateTimePicker';
import celender from "../SideBar/images/calendar.png"
function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}
class ManageActivity extends Component {

    state = {
        value: 0
    }
    handleChange = (event, newValue) => {
        this.setState({ value: newValue })
    };

    handleChangeIndex = (index) => {
        this.setState({ value: index })
    };
    render() {
        let { value } = this.state
        const theme = useTheme
        return (
            <Box sx={{ bgcolor: 'background.paper', width: 750 }}>
                <AppBar position="static">
                    <Tabs
                     sx={{backgroundColor:"#074275"}}
                        value={value}
                        onChange={this.handleChange}
                        indicatorColor="secondary"
                        textColor="inherit"
                        variant="fullWidth"
                        aria-label="full width tabs example"
                    >
                        <Tab label="ACTIVITY" {...a11yProps(0)} />
                        <Tab label="CALL HISTORY" {...a11yProps(1)} />
                        <Tab label="FOLLOW-UP" {...a11yProps(2)} />
                    </Tabs>
                </AppBar>
                <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={value}
                    onChangeIndex={this.handleChangeIndex}
                >
                    <TabPanel value={value} index={0} dir={theme.direction}>
                        <Activity />
                    </TabPanel>
                    <TabPanel value={value} index={1} dir={theme.direction}>
                        <CallTable />
                    </TabPanel>
                    <TabPanel value={value} index={2} dir={theme.direction} >
                       <Box sx={{height:400,width:500}}>
                           <Box sx={{pt:18,pl:42}}>
                          <img src={celender} width="80" height="80"></img><br/>
                          
                          </Box>
                          <Typography variant="subtitle2" sx={{color:"gray",pl:37}}>No Follow ups scheduled</Typography>
                          <Button variant="contained" sx={{ width: 260, height: 35 ,ml:30,backgroundColor:"#074275"}} >Scheduled Follow-up</Button>
                       </Box>
                    </TabPanel>
                </SwipeableViews>
            </Box>
        );
    }

}
export default ManageActivity;