import React, { Component } from "react"
import phone from "./images/phone.png"
import { Box, Collapse, ListSubheader, ListItemButton, StarBorder, Drawer, Typography, Button, List, Divider, ListItem, Grid, ListItemIcon, ListItemText } from '@mui/material';

import "../../assets/App.scss"
import callCenter from "./images/call-centre.png";
import { styled, useTheme } from '@mui/material/styles';

import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import { BsSpeedometer2 } from "react-icons/bs"
import { IoIosArrowUp } from "react-icons/io"
import { FiPhoneCall, FiUsers, FiChevronRight, FiChevronDown } from "react-icons/fi";
import { RiMailSendLine } from "react-icons/ri";
import { connect } from "react-redux"
import {pushClick} from "../../actions"
class Side extends Component {

  state = {
    state: {
      left: false
    },
    logs: false

  }
  toggleDrawer = (anchor, open) => (event) => {
    let s1 = { ...this.state }
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    s1.state.left = open
    this.setState(s1);
  };
  handlelogs = () => {
    let s1 = { ...this.state }

    if (s1.logs == true) {
      s1.logs = false
    } else {
      s1.logs = true
    }

    this.setState(s1)
  }
  handleClick = (index) => {
    let s1 = {...this.state}
    this.props.dispatch(pushClick(index))
    this.toggleDrawer("left", false)
    this.setState(s1)
}

  list = (anchor, logs) => (
    <Box onMouseEnter={this.toggleDrawer(anchor, true)}
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 350, height: 900, backgroundColor:"#074275" }}
      role="presentation"
      onMouseLeave={this.toggleDrawer(anchor, false)}
      onKeyDown={this.toggleDrawer(anchor, false)}
    >
      <div className="mx-4 mt-4">
        <img
          src="https://img.icons8.com/external-flatart-icons-outline-flatarticons/64/000000/external-call-center-agent-sound-design-and-sound-production-flatart-icons-outline-flatarticons.png">
        </img>
        <span className="mx-4 text-secondary ">Switch to old Version</span>
      </div>
      <div className="mt-5 mx-4">
        <img src={phone} width="70" height="70"></img> <span style={{ color: "#93e6a6" }} className=" mx-5">Click to Call</span>
      </div>
      <List

        style={{ color: "white" }} onMouseEnter={this.toggleDrawer(anchor, true)}
      >
        <ListItemButton>
          <ListItemIcon>
            <BsSpeedometer2 className="text-light" />
          </ListItemIcon>
          <ListItemText primary="DashBoard" />
          <ListItemIcon>
            <FiChevronRight className="text-light" />
          </ListItemIcon>
        </ListItemButton>
        <ListItemButton>
          <ListItemIcon>
            <FiPhoneCall className="text-light" />
          </ListItemIcon>
          <ListItemText primary="Live Interactions" />
          <ListItemIcon>
            <FiChevronRight className="text-light" />
          </ListItemIcon>
        </ListItemButton>
        <ListItemButton onClick={this.handlelogs}>
          <ListItemIcon>
            <RiMailSendLine className="text-light" />
          </ListItemIcon>
          <ListItemText primary="Logs" />
          <ListItemIcon>
            {logs == false
              ? <FiChevronDown className="text-light" />
              : <IoIosArrowUp className="text-light" />}
          </ListItemIcon>
        </ListItemButton>
        <Collapse in={logs} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItemButton sx={{ pl: 4, mx: 6 }}  onClick={()=>this.handleClick(0)} >
              <ListItemText primary="Call Logs" />
            </ListItemButton>
            <ListItemButton sx={{ pl: 4, mx: 6 }}>
              <ListItemText primary="SMS Logs"   onClick={()=>this.handleClick(1)} />
            </ListItemButton>
          </List>
        </Collapse>
        <ListItemButton >
          <ListItemIcon>
            <FiUsers className="text-light" />
          </ListItemIcon>
          <ListItemText primary="Customer Management" onClick={()=>this.handleClick(2)}/>
          <ListItemIcon>
            <FiChevronRight className="text-light" />
          </ListItemIcon>
        </ListItemButton>
      </List>

    </Box>
  );
  render() {
    const DrawerHeader = styled('div')(({ theme }) => ({
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-end',
      padding: theme.spacing(0, 1),
      // necessary for content to be below app bar
      ...theme.mixins.toolbar,
    }));
    let { state, logs } = this.state


    return (<div className="text-light" onMouseEnter={this.toggleDrawer("left", true)}>
      <div className="mt-4" >
        {['left'].map((anchor) => (
          <React.Fragment key={anchor} style={{ backgroundColor: "#1d3c77" }}  >
            <img

              src="https://img.icons8.com/external-flatart-icons-outline-flatarticons/64/000000/external-call-center-agent-sound-design-and-sound-production-flatart-icons-outline-flatarticons.png">
            </img>
            <Drawer
              anchor={anchor}
              open={state[anchor]}
              onClose={this.toggleDrawer(anchor, false)}
            >
              {this.list(anchor, logs)}
            </Drawer>
            <DrawerHeader />
          </React.Fragment>
        ))}

      </div>
      <div className="mt-1" >
        <img src={phone} width="70" height="70"></img>
      </div>
      <div className="text-secondary" style={{ fontSize: "15px" }}>
        <div className="mt-5 ">
          <BsSpeedometer2 className=" mx-5 icon-size" />
          DashBoard
        </div>
        <div className="mt-4">
          <FiPhoneCall className="icon-size" /><br />
          Live<br />
          Interactions
        </div>
        <div className="mt-4">
          <RiMailSendLine className="icon-size" /><br />
          Logs
        </div>
        <div className="mt-4">
          <FiUsers className="icon-size" /><br />
          Customer Management
        </div>
      </div>
    </div>)
  }
}
const mapStateToProps = (state) => {
  return { click:state.click }
}
export default connect(mapStateToProps)(Side);