import React, { Component } from "react"
import { Box, Grid, Button, Typography, ListItemButton, ListItemIcon, } from '@mui/material';
import { RiArrowGoBackFill } from "react-icons/ri";
import { FaRegCopy } from "react-icons/fa";
import user from "../SideBar/images/user1.png"
import UserAvtar from "../UserAvatar/UserAvatar";
import "../../assets/App.scss"
import { connect } from "react-redux"
import { IoCall, IoCalendarSharp } from "react-icons/io5";
import { IoIosMail } from "react-icons/io";
import { FaBriefcase } from "react-icons/fa";
import { MdNoteAdd, MdRemoveRedEye, MdWbSunny } from "react-icons/md";
import { BsFillEyeFill, BsPatchCheckFill } from "react-icons/bs";
import ManageActivity from "./manageActivity";
import { pushClick,EditCust,ChangeEdit } from "../../actions";
import { Input, InputLabel, Select, MenuItem, IconButton, InputAdornment, FormControl, TextField } from '@mui/material';
import AccountCircle from '@mui/icons-material/AccountCircle';
import { fontSize } from "@mui/system";
class EditCustomer extends Component {

    state = {
        cust: this.props.customer
    }

    handleClick = (val) => {
        let s1 = { ...this.state }
          if(val==3){
            this.props.dispatch(ChangeEdit(s1.cust,this.props.index))
          }
        this.props.dispatch(pushClick(val))
        this.setState(s1)
    }
    handleChange = (e) => {
        let s1 = { ...this.state }
        let { currentTarget: input } = e
        s1.cust[input.name] = input.value
        console.log(s1.cust)
        this.setState(s1)
    }
    handleChangeWeather = (op) =>{
        let s1 = { ...this.state }
        s1.cust.weather=op
        this.setState(s1)

    }

    render() {
        let { cust } = this.state
        let weatherArr = ["Hot", "Warm", "Cold", "Closed"]
        console.log(cust)
        return (<Box sx={{ flexGrow: 1, backgroundColor: "white" }}>
            <Grid container alignItems="left" spacing={2} sx={{ pt: 2, pl: 7, pb: 1}}>
                <Grid item xs={3} textAlign="left" sm={3} md={2} >
                    <Button className="bg-light" sx={{ color: "#6d1f8c", fontSize: "11px",display: { xs: "none", sm: "none", md: "none", lg:"revert", xl: "revert" } }}>Customers Management</Button>
                </Grid>
                <Grid item xs={3} md={1} sx={{display: { xs: "none", sm: "none", md: "none", lg:"revert", xl: "revert" }}}>

                </Grid>
                <Grid item xs={0} md={6}  sx={{display: { xs: "none", sm: "none", md: "none", lg:"revert", xl: "revert" }}}>
                </Grid>
                <Grid item xs={6} md={3} sx={{ pt: '4px',display: { xs: "none", sm: "none", md: "none", lg:"revert", xl: "revert" } }}>
                    <UserAvtar />
                </Grid>
            </Grid>
            <Grid container spacing={5} sx={{ pl: 2 }}>
                <Grid item sm={12} md={4}>
                    <Grid container spacing={8}>
                        <Grid item xs={2} md={2}>
                            <Button variant="outlined" sx={{ height: 35, width: 70 }} onClick={() => this.handleClick(2)}><RiArrowGoBackFill /></Button>
                        </Grid>
                        <Grid item xs={1} md={3}>
                        </Grid>

                        <Grid item xs={3} md={3}>
                            <Button var3ant="contained" sx={{ width: 90, height: 35,backgroundColor:"#074275" }} onClick={() => this.handleClick(3)}>Save</Button>
                        </Grid>
                        <Grid item xs={3} md={3}>
                            <Button variant="outlined" sx={{ height: 35, width: 90 }} onClick={() => this.handleClick(2)}>Cancel</Button>
                        </Grid>
                    </Grid>
                    <Typography variant="body2" sx={{ fontSize: "12px", mt: 3 }}>PERSONAL INFO</Typography>
                    <TextField
                        sx={{ mt: 2, color: "blue" }}
                        label="Customer Name"
                        fullWidth
                        size="small" AccountCircle
                        name="name"
                        value={cust.name}
                        onChange={this.handleChange}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment>
                                    <IconButton>
                                        <AccountCircle sx={{ color: "blue" }} />
                                    </IconButton>
                                </InputAdornment>
                            )
                        }}
                    />
                    <Grid container spacing={1}>

                        <Grid item sm={2} md={10}>
                            <TextField
                                sx={{ mt: 2, color: "blue" }}
                                label="Phone Number"
                                value={cust.number}
                                name="number"
                                onChange={this.handleChange}
                                fullWidth
                                size="small" AccountCircle
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment>
                                            <IconButton>
                                                <IoCall style={{ color: "blue" }} />
                                            </IconButton>
                                        </InputAdornment>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item sm={2} md={2}>
                        </Grid>
                    </Grid>
                    <Grid sx={{ mt: 2 }}>
                        <Typography variant="caption">BUSINESS INFORMATION</Typography><br />
                        <Typography variant="caption" sx={{ color: "gray" }}>Lead Status</Typography>
                        <Grid container textAlign="center" spacing={2}>
                            {weatherArr.map(op => (
                                <Grid item sx={{ fontSize: "29px" }} xs={3} md={3}>
                                    <ListItemButton  onClick={()=>this.handleChangeWeather(op)} sx={op == cust.weather ? { border: "2px solid blue", borderRadius: "5px", padding: "5px" } : { border: "2px solid #e8eaec", borderRadius: "5px", padding: "5px", backgroundColor: "#f4f7f9", color: "gray" }}>
                                        <Box ><ListItemIcon  sx={{pl:2.4}}>
                                            {op == "Closed" ?
                                                  <BsPatchCheckFill style={{ color: "green", fontSize: "24px" }} />
                                                : <MdWbSunny style={op == "Hot" ? { color: "red" } : op == "Cold" ? { color: "skyblue" } : op == "Warm" ? { color: "yellow" } : {}} />
                                            }</ListItemIcon><Typography sx={op=="Hot"||op=="Cold"?{ fontSize: "14px",pl:2.6 }:{ fontSize: "14px",pl:1.5 }}> {op}</Typography></Box>
                                    </ListItemButton>
                                </Grid>

                            ))}
                        </Grid>
                    </Grid>
                    <TextField
                        sx={{ mt: 2, color: "blue" }}
                        label="Email"
                        name="email"
                        fullWidth
                        onChange={this.handleChange}
                        value={cust.email}
                        size="small" AccountCircle
                        InputProps={{
                            startAdornment: (
                                <InputAdornment>
                                    <IconButton>
                                        <IoIosMail style={{ color: "#150a5b" }} />
                                    </IconButton>
                                </InputAdornment>
                            )
                        }}
                    />
                    <TextField
                        sx={{ mt: 2, color: "blue" }}
                        label="Company Name"
                        fullWidth
                        name="compony"
                        onChange={this.handleChange}
                        value={cust.compony}
                        size="small" AccountCircle
                        InputProps={{
                            startAdornment: (
                                <InputAdornment>
                                    <IconButton>
                                        <FaBriefcase style={{ color: "#150a5b" }} />
                                    </IconButton>
                                </InputAdornment>
                            )
                        }}
                    />
                </Grid>
                <Grid item sm={12} md={8}>
                    <Box sx={{ p: 4, backgroundColor: "#e8eaec",display: { xs: "none", sm: "none", md: "none", lg: "revert", xl: "revert" } }}>
                        <ManageActivity />
                    </Box>
                </Grid>
            </Grid>
        </Box>)
    }

}
const mapStateToProps = (state) => {
    return { customer: state.customer,index:state.index }
}
export default connect(mapStateToProps)(EditCustomer);
