import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import { StepIcon } from '@mui/material';
import {MdPhoneCallback}from "react-icons/md";
const steps = [
  {
    label: 'at 5:20 pm',
    description: `Interested`,
    subDescription :'Call back requested',
    details:'Customer asked to share the plan details for call center for 20 agents'
  },
  {
    label: 'at 8:10 am',
    description: `Interested`,
    subDescription :'Call back requested',
    details:'Deatails Share with cutomers'
  },
  {
    label: 'at 5:20 pm',
    description: `Interested`,
    subDescription :'Call back requested',
    details:'Customer asked to share the plan details for call center for 20 agents'
  },
];

export default function VerticalLinearStepper() {
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <Box sx={{ maxWidth: 600 }}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((step, index) => (
          <Step key={step.label}>
               
            <StepLabel
              
              
            >
             <Typography sx={{color: "gray",pl:3 }} > {step.label}</Typography>
            </StepLabel>
            <StepContent>
              <Typography variant="string" sx={{color: "gray",pl:3 }}> Description :  <Typography variant="string"  sx={{color: "black" }}>{step.description}</Typography></Typography><br/>
              <Typography variant="string" sx={{color: "gray",pl:3 }}> Sub Description : <Typography variant="string"  sx={{color: "black" }}>{step.subDescription}</Typography></Typography>
              <Box sx={{ border: "2px solid #e8eaec", borderRadius: "5px", padding: "5px", backgroundColor: "#f4f7f9",pl:2,mt:3,ml:3 }}>{step.details}</Box>

              <Box sx={{ mb: 2 }}>
                <div>
                  <Button
                    variant="contained"
                    onClick={handleNext}
                    sx={{ mt: 3, mr: 1 ,backgroundColor:"#074275"}}
                  >
                    {index === steps.length - 1 ? 'Last' : 'Next'}
                  </Button>
                  <Button
                    disabled={index === 0}
                    onClick={handleBack}
                    sx={{ mt: 3, mr: 1 }}
                  >
                    Back
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} sx={{ p: 3 }}>
          <Typography>All Details completed If you Read Again then press Reset</Typography>
          <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
            Reset
          </Button>
        </Paper>
      )}
    </Box>
  );
}
