import React, { Component } from 'react';
import { Table,IconButton, TablePagination, List, ListItemButton, ListItem, ListItemIcon, TableBody, TableCell, TableContainer, TableHead, TableRow, Grid, Typography } from '@mui/material';
import Paper from '@mui/material/Paper';
import "../../assets/App.scss"
import user from "../SideBar/images/user1.png"
import { MdWbSunny } from "react-icons/md";
import { BiTime } from "react-icons/bi";
import phone from "../SideBar/images/phone.png"
import { IoCalendarSharp } from "react-icons/io5";
import { IoIosArrowForward } from "react-icons/io";
import { BsFillEyeFill, BsPatchCheckFill } from "react-icons/bs";
import { VscCallOutgoing, VscInfo } from "react-icons/vsc";
import SearchIcon from '@mui/icons-material/Search';
import { Search, SearchIconWrapper, StyledInputBase, BootstrapDialog, BootstrapDialogTitle } from "../Table/tep";
import { connect } from "react-redux"
import { pushClick, EditCust } from "../../actions"

class CustTable extends Component {
    state = {
        rows: this.props.AllCust,
        page: 0,
        rowsPerPage: 10,
        search: "",
        columns: [
            {
                id: 'cust',
                label: "CUSTOMER",
                minWidth: 200,
                align: 'center',
                display: "none",
                format: (value) => value.toLocaleString('en-US'),
            },
            {
                id: 'status',
                label: 'LEAD STATUS',
                minWidth: 140,
                align: 'center',
                display: "show",
                format: (value) => value.toLocaleString('en-US'),
            },

            {
                id: 'created',
                label: 'CREATED ON',
                minWidth: 210,
                align: 'center',
                display: "show",
                format: (value) => value.toLocaleString('en-US'),
            },
            {
                id: 'follow',
                label: 'FOLLOW-UP',
                minWidth: 210,
                align: 'center',
                display: "show",
                format: (value) => value.toLocaleString('en-US'),
            },
            {
                id: 'last',
                label: 'LAST ACTIVITY',
                minWidth: 160,
                align: 'center',
                display: "show",
                format: (value) => value.toFixed(2),
            },
            {
                id: 'action',
                label: 'ACTION',
                minWidth: 160,
                align: 'center',
                display: "none",
                format: (value) => value.toFixed(2),
            },
            {
                id: 'arrow',
                label: '     ',
                minWidth: 2,
                align: 'center',
                display: "none",
                format: (value) => value.toFixed(2),
            },

        ],
    }
    handleChangePage = (event, newPage) => {
        let s1 = { ...this.state }
        s1.page = newPage
        this.setState(s1)
    };

    handleChangeRowsPerPage = (event) => {
        let s1 = { ...this.state }
        s1.rowsPerPage = +event.target.value
        s1.page = 0
        this.setState(s1)
    };
    handleChangeSearch = (e) => {
        let s1 = { ...this.state }
        s1.search = e.currentTarget.value
        this.setState(s1)
    }
    handleClick = (val, index) => {
        let s1 = { ...this.state }
        let row = s1.rows[index]
        this.props.dispatch(pushClick(val))
        this.props.dispatch(EditCust(row,index))
        this.setState(s1)
    }
    render() {
        let { rows, page, rowsPerPage, search, columns } = this.state
        console.log(this.props.AllCust)
        if (search == "") { }
        else {
            {
                rows = rows
                    .filter(item => {
                        if (item.name.includes(search)) {
                            console.log("hy")
                            return true
                        }
                    })
            }
        }
        return (
            <Paper sx={{ width: '100%', overflow: 'hidden' }}>
                <Grid container >
                    <Grid item sm={2} md={2}>  <Typography
                        sx={{ flex: '1 1 100%', pt: 2, pl: 4 }}
                        variant="h5"
                        id="tableTitle"
                        component="div"
                    > All Customers </Typography>
                    </Grid>
                    <Grid item sm={6} md={6}>

                    </Grid>
                    <Grid item sm={4} md={3} sx={{ mt: 3, ml: 5 }} >
                        <Search sx={{ backgroundColor: "#ede9f2" }}>
                            <SearchIconWrapper>
                                <SearchIcon />
                            </SearchIconWrapper>
                            <StyledInputBase
                                value={search}
                                onChange={this.handleChangeSearch}
                                placeholder="Search by Customers"
                                inputProps={{ 'aria-label': 'search' }}
                            />
                        </Search>
                    </Grid>
                </Grid><hr />
                <TableContainer sx={{ maxHeight: 640 }}>
                    <Table stickyHeader size="small" aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                {columns.map((column) => (
                                    <TableCell
                                        key={column.id}
                                        align={column.align}
                                        sx={
                                            column.display == "show"
                                                ? { display: { xs: "none", sm: "none", md: "none", lg: "revert", xl: "revert" } }
                                                : {}}
                                        style={
                                            { minWidth: column.minWidth, fontSize: "12px", display: { xs: "none", sm: "none", md: "none", lg: "none", xl: "none" } }}
                                    >
                                        {column.label}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows
                                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                .map((row, index) => {
                                    return (
                                        <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                            {columns.map((column) => {
                                                const value = row[column.id];
                                                return (
                                                    <TableCell key={column.id} align={column.align}
                                                        sx={
                                                            column.display == "show"
                                                                ? { display: { xs: "none", sm: "none", md: "none", lg: "revert", xl: "revert" } }
                                                                : {}}
                                                    >
                                                        {column.id == "cust"
                                                            ? <Grid container spacing={1}>
                                                                <Grid item xs={2} md={2}>
                                                                    <img src={user} width="28" height="28" />
                                                                </Grid>
                                                                <Grid item xs={10} md={10}>
                                                                    <Typography variant="body2" sx={{ pl: 0.8 }}  >
                                                                        {row.name}
                                                                    </Typography>
                                                                    <Typography variant="body2" sx={{ color: "gray" }}>
                                                                        {row.number}
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                            : column.id == "status"
                                                                ? <Grid container spacing={2}>
                                                                    <Grid item sm={6} md={6} sx={{ fontSize: "27px" }}>
                                                                        {row.weather == "Closed" ?
                                                                            <BsPatchCheckFill style={{ color: "green", fontSize: "22px" }} />
                                                                            : <MdWbSunny style={row.weather == "Hot" ? { color: "red" } : row.weather == "Cold" ? { color: "skyblue" } : row.weather == "Warm" ? { color: "yellow" } : {}} />
                                                                        }
                                                                    </Grid>
                                                                    <Grid item sm={6} md={6} sx={{ mt: 1 }}>
                                                                        <Typography > {row.weather}</Typography>
                                                                    </Grid>
                                                                </Grid>
                                                                : column.id == "created"
                                                                    ? <List >
                                                                        <ListItem >
                                                                            <ListItemIcon className="icon-size text-center">
                                                                                <BiTime className="time-icon mt-2" /><Typography variant="body2">{row.date}<br /> {row.time}</Typography>
                                                                            </ListItemIcon>
                                                                        </ListItem>
                                                                    </List>
                                                                    : column.id == "follow"
                                                                        ? <List >
                                                                            <ListItem >
                                                                                <ListItemIcon className="icon-size text-center">
                                                                                    <IoCalendarSharp className="time-icon mt-2" /><Typography variant="body2">{row.lastDate}<br /> {row.lastTime}</Typography>
                                                                                </ListItemIcon>
                                                                            </ListItem>
                                                                        </List>
                                                                        : column.id == "last"
                                                                            ? <List >
                                                                                <ListItem >
                                                                                    <ListItemIcon className="icon-size text-center">
                                                                                        <Typography variant="body2">{row.lastDate}<br /> {row.lastTime}</Typography>
                                                                                    </ListItemIcon>
                                                                                </ListItem>
                                                                            </List>
                                                                            : column.id == "action"
                                                                                ? <Grid container >
                                                                                    <Grid item>
                                                                                        <img src={phone} width="50" height="50"></img>
                                                                                    </Grid>
                                                                                    <Grid item>
                                                                                        <ListItemButton className="eye-icon rounded-circle" onClick={() => this.handleClick(3, index)}>
                                                                                            <Typography variant="string" className="eye-icon rounded-circle">
                                                                                                <BsFillEyeFill className="eye-icon2 " sx={{ FontFace: 20 }} />
                                                                                            </Typography>
                                                                                        </ListItemButton>
                                                                                    </Grid>
                                                                                </Grid>
                                                                               : column.id == "arrow"
                                                                               ? <IconButton sx={{ display: { xs: "revert", sm: "revret", md: "revert", lg: "none", xl: "none" } }}>
                                                                                   <IoIosArrowForward />
                                                                               </IconButton>
                                                                               : value}



                                                    </TableCell>
                                                )
                                            })}
                                        </TableRow>
                                    );
                                })}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    sx={{ pb: 5, pt: 5 }}
                    rowsPerPageOptions={[5, 10, 15, 20, 25, 100]}
                    component="div"
                    count={rows.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={this.handleChangePage}
                    onRowsPerPageChange={this.handleChangeRowsPerPage}
                />
            </Paper>

        );
    }

}
const mapStateToProps = (state) => {
    return { click: state.click, AllCust: state.AllCust }
}
export default connect(mapStateToProps)(CustTable);
